:root {
  --color-basic-gray: #f9f9f9;
  --switched-color-grayny: #ececec;
  --full-width: calc(100% + 60px);
}
@media (min-width: 768px) {
  :root {
    --switched-color-grayny: #e6e6e6;
    --full-width: calc(100% + 100px);
  }
}
.pixelperfect {
  position: relative;
}
.pixelperfect::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  background-image: var(--pp-img);
  background-repeat: no-repeat;
  background-position: 50% 0;
  transform: translate(var(--pp-offset-x), var(--pp-offset-y));
  opacity: 0.5;
  filter: invert(1);
  pointer-events: none;
}
